.about-kitchen {
    background: $fonLemon;
}

.kitchen-container {
    width: 414px;

    @media (min-width: 1024px) {
        width: 1024px;
        padding: 0 47px;
    }

    @media (min-width: 1280px) {
        width: 1280px;
        padding: 0 85px;
    }
}

.kitchen-top-part {
    @media (min-width: 1024px) {
        display: flex;
        flex-wrap: wrap;
    }
}

.restaurant-slogon {
    font-family: TimesNewRoman;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.15;
    padding-top: 16px;

    color: #B4B4B4;

    @media (min-width: 1024px) {
        display: flex;
        flex-wrap: wrap;
        font-size: 32px;
        padding-top: 120px;
    }
}

.title-name {
    display: flex;
    font-family: FilmP3;
    font-weight: 700;
    font-size: 40px;
    line-height: 1.07;
    letter-spacing: 0.3em;
    margin: 12px 0;
    color: $textColorTitle;

    @media (min-width: 1024px) {
        display: flex;
        flex-wrap: wrap;
        font-size: 64px;
        margin-top: 4px;
        margin-bottom: 0px;

    }
}

.kitchen-info-top {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.25;
    letter-spacing: 0.02em;

    margin-bottom: 28px;
    width: 258px;
    color: $fontColorBase;

    @media (min-width: 1024px) {
        display: flex;
        justify-content: center;
        align-content: center;
        width: 439px;
        height: 80px;
        font-size: 16px;
        margin-top: 24px;
        margin-right: 26px;
    }

    @media (min-width: 1280px) {
        margin-right: 37px;
    }

}

.restaurant-photo-top {
    display: flex;
    margin-left: auto;
    background: #DADADA;
    box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
    border-radius: 2px;
    margin-bottom: 32px;
    width: 244px;

    @media (min-width: 1024px) {
        width: 400px;
        margin-top: -260px;
        z-index: 10;
    }

    @media (min-width: 1280px) {
        width: 460px;
        z-index: 10;
    }
}


.kitchen-bottom-part {
    display: flex;
    align-items: center;

}

.kitchen-photo-bottom {
    display: flex;
    background: #DADADA;
    box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
    border-radius: 2px;

    @media (min-width: 1024px) {
        width: 350px;
    }

    @media (min-width: 1280px) {
        width: 460px;
    }
}

.kitchen-info-bottom {
    font-family: Montserrat;
    font-weight: 600;
    font-size: 12px;
    line-height: 1, 25;
    letter-spacing: 0.02em;
    width: 163px;

    color: $linkColorBase;
    margin-left: 20px;

    @media (min-width: 1024px) {
        display: flex;
        font-size: 16px;
        width: 312px;
        justify-content: center;
        align-items: center;
        margin-left: 48px;
    }
}