.wrapper {
  background-color: #fffdee;
  padding: 15px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 1024px) {
  }
  @media screen and (min-width: 1280px) {
  }
}

.about-hotel {
  outline: solid yellow;
  margin-left: auto;
  margin-right: auto;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  width: 414px;
  max-height: 735px;
  height: 100%;
  max-height: 1191px;

  @media screen and (min-width: 1024px) {
    width: 100%;
    max-width: 1024px;
    max-height: 1238px;
  }
  @media screen and (min-width: 1280px) {
    max-width: 1280px;
  }
}

.about-hotel__info {
  margin-bottom: 32px;

  @media screen and (min-width: 1024px) {
    position: relative;
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    align-items: center;
    gap: 46px;
    margin: 0;
    padding-left: 112px;
    padding-right: 85px;
  }
  @media screen and (min-width: 1280px) {
    gap: 67px;
  }
}
.about-hotel__info-wrapper {
  width: 258px;
  margin-bottom: 28px;
  margin-left: auto;
  text-align: end;
  @media screen and (min-width: 1024px) {
    width: 419px;
  }
  @media screen and (min-width: 1280px) {
    width: 488px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.about-hotel__title-above {
  font-weight: 400;
  font-size: 20px;
  line-height: calc(23 / 20);
  margin-bottom: 12px;
  color: #b8b8b8;

  @media screen and (min-width: 1024px) {
    font-size: 32px;
    line-height: calc(37 / 32);
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 4px;
  }
}

.about-hotel__title {
  display: block;
  font-family: 'FilmP3';
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: calc(43 / 40);
  letter-spacing: 0.3em;
  text-transform: uppercase;
  color: #494848;
  margin-bottom: 16px;
  text-align: right;

  @media screen and (min-width: 1024px) {
    font-size: 64px;
    line-height: calc(69 / 64);
    margin: 0;
    margin-bottom: 24px;
  }
  @media screen and (min-width: 1280px) {
  }
}

.about-hotel__description {
  font-family: 'montserrat';
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: calc(15 / 12);
  color: #7a7a7a;
  text-align: left;

  letter-spacing: 0.2em;

  @media screen and (min-width: 1024px) {
    font-size: 16px;
    line-height: calc(20 / 16);
  }
  @media screen and (min-width: 1280px) {
  }
}

.about-hotel__text {
  font-family: 'montserrat';
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: calc(15 / 12);
  letter-spacing: 0.02em;
  color: #c79002;
  width: 163px;

  @media screen and (min-width: 1024px) {
    font-size: 16px;
    line-height: calc(20 / 16);
    width: 312px;
  }
  @media screen and (min-width: 1280px) {
  }
}

.about-hotel__image-room {
  width: 244px;
  margin-right: auto;
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  @media screen and (min-width: 1024px) {
    width: 400px;
    box-shadow: 5px 5px 70px 17px rgba(0, 0, 0, 0.25);
    margin: 0;
    position: relative;
    top: -44px;
  }
  @media screen and (min-width: 1280px) {
    width: 450px;
  }
}

.about-hotel__lobby {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  gap: 20px;
  margin-bottom: 32px;
  @media screen and (min-width: 1024px) {
    position: relative;
    top: -58px;
    right: 20px;
    gap: 48px;
    margin: 0;
    padding-right: 85px;
  }
  @media screen and (min-width: 1280px) {
    top: -112px;
    gap: 45px;
  }
}

.about-hotel__image-lobby {
  width: 200px;
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  @media screen and (min-width: 1024px) {
    width: 350px;
    box-shadow: 5px 5px 70px 17px rgba(0, 0, 0, 0.25);
  }
  @media screen and (min-width: 1280px) {
    width: 450px;
  }
}

