.hotel__box-shadow {
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
}
.hotel__swiper-container {
  height: auto;
  width: 330px;
  @media screen and (min-width: $tablet) {
    width: 428px;
    position: absolute;
    bottom: 96px;
  }
  @media screen and (min-width: $desktop) {
    width: 530px;
    bottom: 119px;
  }
}
// swiper
.hotel__swiper {
  outline: solid 10px red;
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
  bottom: 0px;
  position: absolute;
  @media screen and (max-width: 1023px) {
    bottom: 0px;
    right: 15px;
  }
}
// swiper-wrapper
.hotel__wrap {
  width: 330px;
  @media screen and (min-width: $tablet) {
    width: 428px;
  }
  @media screen and (min-width: $desktop) {
    width: 530px;
  }
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
}
.hotel__slide {
  height: 245px;
  background-color: #dadada;
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  @media screen and (max-width: $mobile) {
    min-width: 245px;
    height: 245px;
  }
  @media screen and (min-width: $tablet) {
    max-width: 194px;
    height: 194px;
  }
  @media screen and (min-width: $desktop) {
    max-width: 245px;
    height: 245px;
  }
}
.hotel__swiper-arrows {
  position: absolute;
  display: flex;
  gap: 33px;
  bottom: -52px;
  @media screen and (min-width: $tablet) {
    bottom: -68px;
  }
  @media screen and (min-width: $desktop) {
    bottom: -76px;
  }
}
.hotel__button-prev,
.hotel__button-next {
  position: static;
  width: 32px;
  height: 32px;
  cursor: pointer;
}
.hotel__swiper-icon {
  color: #000;
  width: 100%;
  height: 100%;
  display: block;
}
.hotel__swiper-container [class^='swiper-button-']::after {
  content: '';
}
